@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

h1 {
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
}

body {
  width: 100vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  font-family: "Roboto", sans-serif;
}

#reset {
  cursor: pointer;
  border: none;
  position: relative;
  font-size: 24px;
  top: 2px;
}
#reset:hover {
  background-color: transparent;
  box-shadow: 0 0 10px rgb(219, 243, 255);
}
#reset:hover::after {
  content: "reset";
  color: rgb(0, 140, 255);
}

.container {
  background-color: #ffffff;
  width: 1440px;
  padding-top: 96px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}
.container .module {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 96px;
  flex-wrap: wrap;
}
.container .module .main__image {
  width: 505px;
  height: 330px;
  background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.container .module .aside {
  width: 380px;
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.container .module .aside__header--title {
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}
.container .module .aside__content {
  margin: 24px 0 32px 0;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}
.container .module .aside__button--open-modal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 128px;
  height: 48px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
  background: #E4C1AE;
  border-radius: 24px 24px;
  border: none;
}
.container .module .aside__button--open-modal:hover {
  cursor: pointer;
  box-shadow: 0px 0px 8px #b5b5b5;
  color: rgb(0, 140, 255);
  background-color: #8a5c44;
}
.container .modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: rgba(18, 18, 18, 0.5215686275);
  opacity: 0;
  visibility: hidden;
  transition: all 100ms ease-in;
  font-family: "Roboto", sans-serif;
}
.container .modal.is-open {
  visibility: visible;
  opacity: 1;
}
.container .modal__dialog {
  position: relative;
  width: 920px;
  height: 210px;
  background-color: #ffffff;
  padding: 16px 16px 16px 96px;
  opacity: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 24px;
}
.container .modal__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}
.container .modal__content {
  width: 380px;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}
.container .modal__content--results {
  font-weight: 700;
}
.container .modal__content--reset {
  color: rgb(28, 34, 152);
}
.container .modal--close-modal {
  position: absolute;
  top: 46px;
  right: 46px;
  border: none;
  background-image: url("../images/cross.svg");
  background-color: #ffffff;
  width: 24px;
  height: 24px;
}
.container .modal--close-modal:hover {
  background-image: url("../images/cross_red.svg");
}
.container .modal__content--reset.hide {
  opacity: 0;
  visibility: hidden;
}

@media screen and (max-width: 1234px) {
  .container .module {
    gap: 64px;
  }
  .container .module .main__image {
    width: 80vw;
    height: 53.3333333333vw;
    background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1234.jpg");
  }
  .container .module .aside {
    width: 80vw;
  }
  .container .modal__dialog {
    width: 840px;
    padding-left: 96px;
  }
  .container .modal__header {
    font-size: 42px;
  }
}
@media screen and (max-width: 1002px) {
  .container {
    padding-top: 64px;
  }
  .container .module {
    gap: 64px;
  }
  .container .module .main__image {
    width: 80vw;
    height: 53.3333333333vw;
    background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1002.jpg");
  }
  .container .module .aside {
    width: 80vw;
  }
  .container .module .aside__header--title {
    font-size: 32px;
  }
  .container .modal__dialog {
    width: 740px;
    padding-left: 64px;
  }
  .container .modal__header {
    font-size: 32px;
  }
}
@media screen and (max-width: 784px) {
  .container {
    padding-top: 48px;
  }
  .container .module {
    gap: 32px;
  }
  .container .module .main__image {
    width: 85vw;
    height: 56.6666666667vw;
    background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg");
  }
  .container .module .aside {
    width: 85vw;
  }
  .container .module .aside__header--title {
    font-size: 28px;
  }
  .container .modal__dialog {
    width: 400px;
    padding-left: 32px;
  }
  .container .modal__header {
    font-size: 32px;
  }
  .container .modal__content {
    width: 340px;
  }
}
@media screen and (max-width: 480px) {
  .container {
    padding-top: 16px;
  }
  .container .module {
    gap: 24px;
  }
  .container .module .main__image {
    width: calc(100vw - 16px);
    height: calc((100vw - 16px) / 1.5);
    background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_480.jpg");
  }
  .container .module .aside {
    width: calc(100vw - 16px);
  }
  .container .module .aside__header--title {
    font-size: 24px;
  }
  .container .modal__dialog {
    width: 340px;
    padding-left: 16px;
  }
  .container .modal__header {
    font-size: 32px;
  }
  .container .modal__content {
    width: 340px;
  }
}